<template>
  <div class="login-wrapper">
    <div class="login">
      <div class="login-inner">
        <div class="bar2">
          <el-input v-model="username" placeholder="用户名/邮箱" clearable @keyup.enter.native="toLogin"></el-input>
        </div>
        <div class="bar3">
          <el-input
            placeholder="密码"
            v-model="password"
            show-password
            clearable
            @keyup.enter.native="toLogin"
          ></el-input>
        </div>
        <el-button type="primary" style="width:136px" @click="toLogin" :loading="loading">登录<i class="el-icon-right el-icon--right"></i></el-button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters, mapMutations, mapActions } from "vuex";
export default {
  name: "Login",
  data() {
    return {
      type: "seller",
      loading: false,
      value: "",
      username: "",
      password: ""
    };
  },
  computed: {},
  created() {
    document.body.style.backgroundColor = "#E8EAF6";
  },
  destroyed() {
    document.body.style.backgroundColor = "#fff";
  },
  methods: {
    ...mapActions(["login"]),
    toLogin() {
      this.loading = true;
      this.login({
        username: this.username,
        password: this.password
      })
        .then(res => {
          this.loading = false;
          this.$router.push("/");
        })
        .catch(e => {
          this.loading = false;
          this.$message.error("登录失败");
        });
    }
  }
};
</script>

